___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 14px;
}

body {
  background-color: #f5f5f5;
  width: 37rem;
  margin: 0 auto;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  border: 1px solid #ccc;
}

.leaderboard {
  width: 100%;
  border-collapse: collapse;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  margin: 10px;
}

.grid > div {
  padding: 10px;
}

.add-score {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}

label {
  display: inline-block;
  margin-bottom: 1.7rem;
  margin-right: 1rem;
}

ul {
  list-style: none;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  border: 1px solid #ccc;
}

ul li:nth-child(even) {
  background-color: lightgray;
}

input[type=text] {
  margin-bottom: 1.7rem;
  padding: 0.5rem;
  border: 1px solid #ccc;
}

#submit-btn {
  padding: 5px;
  border: 1px solid #ccc;
  background-color: #fff;
  cursor: pointer;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.scss"],"names":[],"mappings":"AACA;EACE,sBAAA;EACA,SAAA;EACA,UAAA;EACA,2DAAA;EACA,eAAA;AAAF;;AAGA;EACE,yBAAA;EACA,YAAA;EACA,cAAA;AAAF;;AAGA;EACE,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,gBAAA;EACA,sBAAA;AAAF;;AAGA;EACE,WAAA;EACA,yBAAA;AAAF;;AAGA;EACE,aAAA;EACA,8BAAA;EACA,cAAA;EACA,YAAA;AAAF;;AAGA;EACE,aAAA;AAAF;;AAGA;EACE,aAAA;EACA,sBAAA;EACA,mBAAA;EACA,mBAAA;AAAF;;AAGA;EACE,qBAAA;EACA,qBAAA;EACA,kBAAA;AAAF;;AAGA;EACE,gBAAA;EACA,eAAA;EACA,aAAA;EACA,sBAAA;EACA,WAAA;EACA,sBAAA;AAAF;;AAGA;EACE,2BAAA;AAAF;;AAGA;EACE,qBAAA;EACA,eAAA;EACA,sBAAA;AAAF;;AAGA;EACE,YAAA;EACA,sBAAA;EACA,sBAAA;EACA,eAAA;AAAF","sourcesContent":["\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  font-family: Arial, \"Helvetica Neue\", Helvetica, sans-serif;\n  font-size: 14px;\n}\n\nbody {\n  background-color: #f5f5f5;\n  width: 37rem;\n  margin: 0 auto;\n}\n\n.container {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  margin-top: 20px;\n  border: 1px solid #ccc;\n}\n\n.leaderboard {\n  width: 100%;\n  border-collapse: collapse;\n}\n\n.grid {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  grid-gap: 10px;\n  margin: 10px;\n}\n\n.grid > div {\n  padding: 10px;\n}\n\n.add-score {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  margin-bottom: 2rem;\n}\n\nlabel {\n  display: inline-block;\n  margin-bottom: 1.7rem;\n  margin-right: 1rem;\n}\n\nul {\n  list-style: none;\n  padding: 1.5rem;\n  display: flex;\n  flex-direction: column;\n  gap: 0.5rem;\n  border: 1px solid #ccc;\n}\n\nul li:nth-child(even) {\n  background-color: lightgray;\n}\n\ninput[type=\"text\"] {\n  margin-bottom: 1.7rem;\n  padding: 0.5rem;\n  border: 1px solid #ccc;\n}\n\n#submit-btn {\n  padding: 5px;\n  border: 1px solid #ccc;\n  background-color: #fff;\n  cursor: pointer;\n}\n"],"sourceRoot":""}]);
